import React from 'react';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";

const ListAlbums = props => {
    return (
        <div className='row'>
            {//Get list albums through props
                props.items.map((item) => {
                    return (
                        <div key={item.id} className='col-md-2' >
                            <Link to={"/kinespotify/album/" + item.id} onClick={() => props.getData(item)}>
                                <div className="card">
                                    <img className="card-img-top" src={item.images[0].url} alt="Cover" />
                                    <div className="card-body">
                                        <h6 className="card-title">{item.name}</h6>
                                        <p className="card-text">{item.artists[0].name}</p>
                                    </div>
                                </div>
                            </Link>
                        </div>
                    )
                }
                )
            }
        </div>
    )
}

export default ListAlbums
