import React, { Component } from 'react';
import './Album.css';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import loading from '../../img/loading.gif'

export class Album extends Component {
    constructor(props) {
        super(props);
        this.state = {
            album: '',
            isLoad: false
        };
    }

    render() {

        //Loading view appears while data is getting from API
        if (this.state.isLoad === false) {
            return (
                <div className='loading'>
                    <div className='img-load'>
                        <img className='imgLoad' src={loading} alt='loading' />
                        <h2>Loading...</h2>
                    </div>
                </div>
            )

        }

        //View after get data from API
        else {
            return (
                <div className='album'>
                    <h1>Albums</h1>
                    <div className='row'>
                        {//Get list albums by map
                            this.state.album.map((item, index) => {
                                return (
                                    <div key={index} className='col-md-2'>
                                        <Link to={"/kinespotify/album/" + item.album.id} onClick={() => this.props.getData(item.album)} >
                                            <div className="card">
                                                <div className='album-wrap'>
                                                    <img className="card-img-top" src={item.album.images[0].url} alt="Cover" />
                                                </div>

                                                <div className="card-body">
                                                    <h6 className="card-title">{item.album.name}</h6>
                                                    <p className="card-text">{item.album.artists[0].name}</p>
                                                </div>
                                            </div>
                                        </Link>
                                    </div>
                                )
                            }
                            )
                        }
                    </div>
                </div>
            )
        }
    }

    // Get item from API
    componentDidMount = () => {
        const token = this.props.accessToken;
        const url = `https://api.spotify.com/v1/me/albums?limit=10&offset=0`;
        fetch(url, {
            //config header
            headers: {
                "Content-Type": "application/json",
                "Accept": "application/json",
                "Authorization": `Bearer ${token}`,
            }
        }).then(response => response.json())
            .then(data => {
                console.log(data);
                this.setState({
                    isLoad: true,
                    album: data.items,
                    isLoad: true
                })
            })
            .catch(e => {
                console.log('error', e)
            });
    }
}


export default Album
