import React, { Component } from 'react';
import './Bottom.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPause, faCheck, faRandom, faStepBackward, faStepForward, faPlayCircle, faRetweet, faListAlt, faDesktop, faVolumeUp } from '@fortawesome/free-solid-svg-icons';

let audio = new Audio();
export class Bottom extends Component {
    constructor(props) {
        super(props);
        this.state = {
            artist: '',
            cover: '',
            track: '',
            playing: '',
            isPause: true
        };
    }

    componentWillReceiveProps(nextProps) {

        if (nextProps.playing !== "") {
            console.log(nextProps.playing);
            if (this.state.playing !== nextProps.playing.preview_url) {
                this.setState({
                    artist: nextProps.playing.artists[0].name,
                    cover: nextProps.playing.album.images[0].url,
                    playing: nextProps.playing.preview_url,
                    track: nextProps.playing.name,
                    isPause: false
                }, () => {
                    if(this.state.isPause === false){
                        if (audio) {
                            audio.pause();
                        }
                        audio = new Audio(this.state.playing);
                        audio.play()
                    }
                });
            }

        }
    }

    pauseTrack() {
        if(this.state.playing !== ''){
            if (this.state.isPause) {
                this.setState({
                    isPause: false
                }, () => audio.play())
            }
            else {
                this.setState({
                    isPause: true
                }, () => audio.pause())
            }
        }    
    }

    render() {
        //style for setting cover on player bar
        const coverImg = {
            backgroundImage: `url(${this.state.cover})`
        };
        return (

            <div className='bottom row'>
                {/* Info side */}
                <div className='info col-md-4'>
                    <div style={coverImg} className='cover'>
                    </div>

                    <div>
                        <h5>{this.state.track}</h5>
                        <h6>{this.state.artist}</h6>
                    </div>
                    <FontAwesomeIcon className='icon checkIcon' icon={faCheck} color="#439e65" />
                </div>

                {/* Control side */}
                <div className='col-md-5 play-bar'>
                    <div className='control-button'>
                        <FontAwesomeIcon className='icon' icon={faRandom} />
                        <FontAwesomeIcon className='icon' icon={faStepBackward} />
                        {
                            this.state.isPause ?
                                <FontAwesomeIcon onClick={() => { this.pauseTrack() }} className='icon play-btn' icon={faPlayCircle} />
                                :
                                <FontAwesomeIcon onClick={() => {this.pauseTrack() }} className='icon play-btn' icon={faPause} />
                        }


                        <FontAwesomeIcon className='icon' icon={faStepForward} />
                        <FontAwesomeIcon className='icon' icon={faRetweet} />

                    </div>
                    <div className='bar'>
                        <span>00:00</span>
                        <div className="progress">
                            <div className="progress-bar w-75" role="progressbar" aria-valuenow="75" aria-valuemin="0" aria-valuemax="100"></div>
                        </div>
                        <span>00:30</span>
                    </div>

                </div>

                {/* Option side */}
                <div className='col-md-3 control-thing'>
                    <FontAwesomeIcon className='icon' icon={faListAlt} />
                    <FontAwesomeIcon className='icon' icon={faDesktop} />
                    <FontAwesomeIcon className='icon' icon={faVolumeUp} />
                    <div className="progress">
                        <div className="progress-bar w-50" role="progressbar" aria-valuenow="75" aria-valuemin="0" aria-valuemax="100"></div>
                    </div>
                </div>
            </div>
        )

    }
}

export default Bottom
