import React, { Component } from 'react';
import Middle from '../component/Middle/Middle';
import RightSide from '../component/RightSide/RightSide';

export class ListTrack extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isPlay: '',
            choosenTrack: '',
            id: this.props.match.params.id
        };
        this.playStop = this.playStop.bind(this);
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.match.params.id) {
            this.setState({
                id: nextProps.match.params.id
            })
        }

    }

    playStop(dataFromChild) {
        if (dataFromChild === true) {
            this.setState({
                isPlay: dataFromChild,
            }, () => console.log(this.state));
        }
        else if (dataFromChild === false) {
            this.setState({
                isPlay: dataFromChild,
            }, () => console.log(this.state));
        }
    }

    render() {
        const style = {
            height: '100%',
            width: '100%'
        };
        return (   
            <div style={style} className='row'>
                <div className='col-md-5'>
                    <Middle album={this.props.album} id={this.state.id} accessToken={this.props.accessToken} />
                </div>
                <div className='col-md-7'>
                    <RightSide getPlaying={this.props.getPlaying} id={this.state.id} accessToken={this.props.accessToken} />
                </div>
            </div>
        )
    }
}

export default ListTrack
